.autologin_label {
    position: relative;
}
.autologinbutton {
    opacity: 0;
}
.autologin_label:after {
    position: absolute;
    top: .25rem;
    /*top: 0;*/
    left: -1.1rem;
    display: block;
    width: 1rem;
    height: 1rem;
    content: "";
    background: no-repeat 50%/50% 50%;
}
.autologin_label:before {
    content: "";
    border-radius: .25rem;
    border: 1px solid #adb5bd;
    background-color: #fff;
    pointer-events: none;
    width: 1rem;
    height: 1rem;
    position: absolute;
    top: .25rem;
    left: -1.1rem;
    display: block;
    -webkit-transition: background-color .15s ease-in-out, border-color .15s ease-in-out, -webkit-box-shadow .15s ease-in-out;
    transition: background-color .15s ease-in-out, border-color .15s ease-in-out, -webkit-box-shadow .15s ease-in-out;
    transition: background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out;
    transition: background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out, -webkit-box-shadow .15s ease-in-out;
}
.label_checked:after {
    background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3E%3Cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26l2.974 2.99L8 2.193z'/%3E%3C/svg%3E");
}
.label_checked:before {
    color: #fff;
    background-color: #08bf91;
    border-color: #08bf91;
}

/* 
https://stackoverflow.com/questions/10782054/what-does-the-tilde-squiggle-twiddle-css-selector-mean
for more ~ selector information.
 */
.autologinbutton:active~.autologin_label:before {
    color: #fff;
    background-color: #80fadb;
    border-color: #80fadb;
}
.autologinbutton:focus~.autologin_label:before {
    -webkit-box-shadow: 0 0 0 0.2rem rgba(8,191,145,.25);
            box-shadow: 0 0 0 0.2rem rgba(8,191,145,.25);
}

